.wp-block-cjp-blocks-conclusion {
	background-color: rgba(0, 0, 0, 0);
	padding: 1px 1rem;
	box-sizing: border-box;
	max-width: 100%;
	transition: background-color 0.3s ease-in;
	margin: 0;

	img {
		max-width: 100%;
		height: auto;
	}
}

.wp-block-cjp-blocks-conclusion.background-clear {
	background-image: linear-gradient(90deg, #ffffff 1rem, rgba(0, 0, 0, 0) 1rem);
}

.wp-block-cjp-blocks-conclusion.background-peach-800 {
	background-image: linear-gradient(90deg, #8f5f5a 1rem, rgba(0, 0, 0, 0) 1rem);
}

.wp-block-cjp-blocks-conclusion.background-blue-600 {
	background-image: linear-gradient(90deg, #60656c 1rem, rgba(0, 0, 0, 0) 1rem);
}

.wp-block-cjp-blocks-conclusion.background-peach-100 {
	background-image: linear-gradient(90deg, #f1e1d9 1rem, rgba(0, 0, 0, 0) 1rem);
}
